import React from 'react';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import MyPostsContainer from './MyPosts/MyPostsContainer';
import axios from 'axios';
const instance = axios.create({
  withCredentials: false,
  headers: {
    'mode': 'no-cors',
    "client_id": "xxxx",
    "client_secret": "xxxx",
    "grant_type": "authorization_code",
    "code": "xxxxxxx",
    "redirect_uri": "https://test.test"
  },
  baseURL: 'https://developers.amocrm.ru/developers/oauth2/access_token',
});
const Profile = (props) => {

  return <div>
    <ProfileInfo
      profile={props.profile}
      userId={props.userId}
      ownerId={props.ownerId}
      userStatus={props.userStatus}
      updateUserProfileStatus={props.updateUserProfileStatus}
      updateAboutMe={props.updateAboutMe}
      savePhoto={props.savePhoto}
      updateUserPhoto={props.updateUserPhoto}
      editModeAboutMe={props.editModeAboutMe}
      key={props.key}
    />
    <MyPostsContainer />
  
  </div>

}

export default Profile;